export const styles = css`:host igc-dialog[open]::part(base){border-radius:.375rem;-webkit-box-shadow:var(--ig-elevation-5);box-shadow:var(--ig-elevation-5);border:.0625rem solid var(--border-color)}:host igc-focus-trap{-webkit-box-shadow:0 0 0 .0625rem var(--border-color);box-shadow:0 0 0 .0625rem var(--border-color)}:host [part~=label]{font-family:var(--ig-caption-font-family, var(--ig-font-family));font-size:var(--ig-caption-font-size);font-weight:var(--ig-caption-font-weight);font-style:var(--ig-caption-font-style);line-height:var(--ig-caption-line-height);letter-spacing:var(--ig-caption-letter-spacing);text-transform:var(--ig-caption-text-transform);margin-top:var(--ig-caption-margin-top);margin-bottom:var(--ig-caption-margin-bottom)}:host [part=helper-text]{padding:0}[part=actions]{min-height:max(var(--is-large, 1) * max(3rem, -1 * 3rem), var(--is-medium, 1) * max(2.75rem, -1 * 2.75rem), var(--is-small, 1) * max(2.5rem, -1 * 2.5rem));padding:.5rem 1rem}:host(:focus-within) [part=label]{color:var(--focused-secondary-color)}`;
